*{
  margin: 0;
}

.app {
  background-color: #111;

}
.nav{
  position: fixed;
  top:0;
  display:flex;
  justify-content: space-between;
  width: 100%;
  height:60px;
  z-index: 1;
  transition-timing-function: ease-in;
  transition:all 0.5s
}

.nav__results{
  height: 60px !important;
  background-color: #111;
}


.nav_black{
  background: #111
}
.nav_logo{
  position: fixed;
  left: 20px;
  width:90px;
  object-fit: contain;
 cursor: pointer;
}


.nav_avatar{
  position: fixed;
  right:20px;
  width:30px;
  object-fit: contain;
  padding-top:10px;
  cursor: pointer;
}

.search,
.search_white{
  margin: auto;
  position: fixed;
  left: 33%;
  top: 1.5%;
}

.search i,
.search_white i{
  margin-left: 3px;
  cursor: pointer;
  
}

.search_white i{
  color: white;
}

.input{
  height: 37px;
  width: 250px;
  border-radius: 5px;
  padding-left: 30px;
  color: white;
  background-color: transparent;
  border: 1px solid darkgrey;
  margin: auto;
}

.input::-webkit-input-placeholder{
  color: white;
  
}

.input:-ms-input-placeholder{
  color: white;
  
}

.input::-ms-input-placeholder{
  color: white;
  
}

.input::placeholder{
  color: white;
  
}


.dropdown{
margin-top: 60px;
  color: white;
  margin-right: 10px;
  position: fixed;
  left:82%
}

.hide{
  display: none;
}
.dropdown__ul{
  list-style-type: none;
  background-color: #111;
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
}

.dropdown ul li{
  margin-top: 3px;
  margin-bottom: 3px;
  padding: 5px;
  cursor: pointer;
}

.user li{
  margin-left: 6px;
}

.user{
  display: flex;
  margin: 5px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.logout{
  margin-top:15px !important;
  margin-left: 3px;
}

@media(max-width:730px){
  .dropdown{
    left: 80%;
  }
}

@media(max-width:600px){
  .search,.search_white{
    display: none;
  }
  .dropdown{
    left: 71%;
  }
}

@media(max-width:550px){
  .dropdown{
    left: 65%;
  }
}

@media(max-width:390px){
  .dropdown{
    left: 55%;
  }
}

.banner {
  color: white;
  height: 448px;
  object-fit: contain;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.banner__contents {
  margin-left: 30px;
  padding-top:140px;
  height:190px;
}
.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__title:hover{
  color: #111;
}
 
.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: .2vw;
  margin-right: 1rem;
  background-color: rgba(51,51,51,.5);
  padding: .5rem 2rem;
}
.banner__button:hover{
  color: #000;
  background-color: #e6e6e6;
  /* transition: all .2s; */
}

.banner__description {
  max-width: 360px;
  line-height: 1.3;
  /* put spacing between lines */
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3; /* number of lines to show */
  -webkit-box-orient: vertical;
  font-weight:bold;
  font-size:12px;
  margin-top: 20px;
}

.banner--fadeBottom{
  height:7.4rem;
  background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.50), #111);
}
.row {
  margin-left: 20px;
  color: white;
}
.scrollContainer {
  width: 50%;
}
.row__posters {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 20px;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  transition: transform 450ms;
  margin-right: 10px;
  cursor: pointer;
}
.row__poster:hover {
  transform: scale(1.08);
}
.row__posterLarge {
  max-height: 250px;
  cursor: pointer;
}
.row__posterLarge:hover {
  transform: scale(1.09);
}

.posters{
  display: grid !important;
  width: 100%;
 grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  margin-top: 60px !important;
  grid-row: column gap 10px !important;
  overflow-x: hidden;
  background-color: #111;
}



.poster{
  object-fit: contain;
  width: 100%;
  transition: transform 450ms;
  margin-right: 10px;
  margin-top: 10px;
  border-radius:15px;
  border: 1px solid transparent;
  cursor: pointer;
}
.poster:hover {
  transform: scale(1.08);
}
.posterLarge {
  max-height: 250px;
}
.posterLarge:hover {
  transform: scale(1.09);
}

.overview{
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;  /* Number of lines displayed before it truncate */
   overflow: hidden;
   margin-top: 10px;
}

.name-date{
  display: flex;
  margin: 8px;
  justify-content: space-between;
}

.name-date h5{
  background-color: yellow;
  padding: 3px;
  color: #111;
  border-radius: 2px;
}

.rating{
  background-color: yellow;
  padding: 3px;
  color: #111;
  border-radius: 2px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 8px;
}

.rating-utube{
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.iframe{


}

#iframe{
  object-fit: contain;
}

@media(max-width:850px){
  .posters{
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

@media(max-width:670px){
  .posters{
    grid-template-columns: 1fr 1fr 1fr ;
  }
}

@media(max-width:550px){
  .posters{
    grid-template-columns:  1fr 1fr ;
  }
}

@media(max-width:370px){
  .posters{
    grid-template-columns:  1fr;
    margin-top: 40px !important;
    overflow-x: hidden;
  }
  .iframe{
    width: 300px;
    height: 500px;

  }

}
:root{
    --netflix-red:  #e50914;    
}

.container{
        font-family: Nunito Sans, sans-serif;
        font-weight: bold;
        margin: 0;
        background-color: black;
        color: white;
        overflow-x: hidden;
}

.font-light{
    font-weight: 400;
}
.spacer{
    background-color: #222;
    height: .5rem;
}


header{
    padding-top: 1.75rem;   
    margin: 0 auto;    
}

#header-logo{
    max-width: 10.47rem;
    margin-left: 3.5rem;    
}

#header-signin{    
    margin-right: 3.5rem;
    float: right;  
    padding: 0.35rem 1rem;
    text-decoration: none;
    color: white;    
    background-color: #e50914;    
    background-color: var(--netflix-red) ;
    border-radius: 0.18rem;    
}

#home-wrapper{          
    background-image: url(/static/media/bg-titles.55011567.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: inset 0 0 1000px 100px  black;
    padding-bottom: 8rem;
}
#center-wrapper{   
    margin: 10.65rem auto 0;
    max-width: 50rem;
    text-align: center;    
}
#center-wrapper h1{
    margin: 0;
    font-size: 4.2em;
    line-height: 1em;
    color: white;
}
#center-wrapper h2{    
    margin: 1.12rem 0 0 0;
    font-size: 1.7em;
    color: white;
}



/* form */
.form-wrapper{       
    max-width: 47.62rem;    
    display: flex;
    flex-direction: column;    
}
.form-wrapper form{
    display: flex;
    padding-top: 1.75rem;
}
.email{
    width: 65.5%;
    height: 4.25rem;
    padding: 0;
    margin: 0 auto;
    border: 1px darkgrey solid;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    text-indent: 0.62rem;
    font-size: 1.1rem;    
}
.email-submit{
    width: 34.2%;  
    height: 4.37rem;    
    margin: 0 auto;
    border: none;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    border-left: 1px grey solid;
    background-color: #e50914;
    background-color: var(--netflix-red);          
}
.email-submit:hover{
    background-color: #f40612;
    cursor: pointer;
}

.submit-div p{
    display: inline;
    color: white;
    padding: 0;
    margin: 0;
    padding-right: 0.5rem;
    font-size: 1.8rem;
}
.icon{
    height: 1.25rem;
}
.p-under-form{    
    font-size: 1.18em;
    font-weight: 400;
    text-align: center;
    width: 100%;
    margin-bottom: 0;
}


/* ENJOY TV */
/* sidetext */
.section-wrapper{
    padding: 1rem 0 3rem;
}
.section-center-wrapper{
    display: flex;
    justify-content: center;
    background-color: black;
    padding: 0 4rem;
}
.section-sidetext{    
    max-width: 34rem; 
    margin: auto 0;             
    padding: 2rem 3.5rem 0 1.5rem;   
    text-align: left;   
    text-align: initial;    
}
.section-sidetext h1{
    margin: 0 0 0.6rem;
    font-size: 3.2rem;
    line-height: 3.5rem;
}
.section-sidetext h2{
    font-size: 1.7rem;
    margin-bottom: 0;
}/*  /sidetext  */

/* tv animation*/
#tv-animation{
    max-width: 530px;
    width: 100%;
    height: 100%;    
    transform: translateY(0%);
    margin: auto 0;  
}
#tv-overlay{
    width: 100%;   
}
#tv-video{
    position: absolute;    
    width: 72.4%;    
    left: 13.3%;
    top: 20.6%;  
}/*  /tv animation*/
/*  /ENJOY TV  */


/*  DOWNLOAD  */
#mobile-wrapper{
    position: relative;
    order: 1;
    margin:auto 6rem auto 0;
    height: 100%;
    min-width: var(--min-width);

}
#download-sidetext{
    order: 2;
}
/*  mobile  */
#mobile{  
    max-width: 504px;
    width: 100%;    
}
#mobile-download{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 10%;
    display: flex;    
    min-width: 264px;
    width: 60%;
    padding: .5rem;
    margin: 0 auto;
    border: 2px solid #444;
    border-radius: 13px;
    background-color: black;
    box-shadow: 0 0 15px 2px black;    
}
#show-cover{
    max-width: 3.57rem;   
}
#mobile-download-text{   
    max-width: 14rem;
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
#mobile-download-text h3{
    margin: 0;
    font-size: 1rem;
}
#mobile-download-text h4{
    margin: 0;
    font-size: .9rem;
    font-weight: lighter;
    color: #1c5cfc;
    
} /*  /mobile  */
/*  mobile animation  */
#mobile-download-animation{
    max-width: 1.5rem;
    width: 100%;
    align-self: center;
    margin-bottom: 1.5rem;
}
.animation-element{
    width: 1.5rem;
    position: absolute;
    right: 1rem;    
    opacity: 0;
    animation-iteration-count: infinite;
}

#download-icon{   
    animation: download-animation 7s infinite;  
    width: 1.2rem;    
}
@keyframes download-animation{
    0%{opacity: 1;}
    18%{opacity: 1;}
    20%{opacity: 0;}
}
#loading-icon{   
    animation: loading-animation 7s infinite;
    animation-timing-function: linear;
}
@keyframes loading-animation{
    19%{opacity: 0;
        transform: rotate(900deg);}
    20%{opacity: 1;}
    78%{opacity: 1;}
    80%{opacity: 0;
        transform: rotate(0deg);}
}
#smartphone-icon{    
    animation: smartphone-animation 7s infinite;
}
@keyframes smartphone-animation{   
    79%{opacity: 0;}
    80%{opacity: 1;}
    99%{opacity: 1;}
    100%{opacity: 0;}
}/* /mobile animation */
/* /DOWNLOAD  */


/*  DEVICES  */
#devices-animation{
    position: relative;   
    width: 100%;
    max-width: 550px;
    height: 100%;   
    margin: auto 0;
    padding-top: 2rem;
}
#devices-video{
    position: absolute;
    width: 61.23%;
    height: auto;
    right: 20%;
    top: 17%;
    z-index: 1;
}
#devices-overlay{
    position: relative;
    width: 100%;
    z-index: 1;
}
/*  /DEVICES  */



/*  FAQ  */
/* faq elements*/
#faq-wrapper{
    display: block;
    margin: 0 auto;   
}
#faq-wrapper-content{
    width: 45%;
    min-width: 700px;
    margin: 0 auto;
}
#faq-wrapper > h1{
    text-align: center;
    margin: 0;
    padding: 4rem 0 2.2rem;
    font-size: 3.2rem;
}
.faq-element{
    background-color: #333 ;
    margin-top: .5rem;    
}
.faq-element:hover{
    cursor: pointer;
}
.faq-title h1{
    font-size: 1.6rem;
    font-weight: 600;
    margin: 1.27rem 2rem ;
    display: inline-block;
}
.faq-title img{
    float: right;
    width: 1.5rem;
    margin: 1.5rem;
} /*  /faq elements  */
/* faq form*/
#faq-form{
    margin: 2rem auto 0;
}/*  /faq form */
/* /FAQ   */


/*  FOOTER  */
#footer-wrapper{
    color: #757575;  
    margin: 4.5rem auto 0 auto;
    padding: 0;
}
#footer-center-wrapper{    
    height: auto;   
    
    max-width: 56rem;
    display: block;
    margin: 0 auto;
}
/*  footer links */
#contact-number{   
   color: inherit;
   font-weight: 600;
   margin-bottom: 1.7rem;   
   margin-left: auto;
}
#contact-number-link{
    color: inherit;
    text-decoration: none;
}
#footer-center-wrapper a:hover{
    text-decoration: underline;
}
#footer-links{
    list-style-type: none;        
    display: flex;
    flex-wrap: wrap;    
    font-weight: 500;
    padding: 0;
}
.footer-link-container{     
    width: 25%;
    min-width: 135px;
    margin-bottom: 0.63rem;
    
}
.footer-link-element{
    color: inherit;
    text-decoration: none;
    font-size: 0.8rem;
}
/*  /footer links*/
/*  language selector  */
#language-selector{
    position: relative;    
    width: 8.5rem;    
}
#language-selector::before{
    content: url(data:image/png;base64,);
    position: absolute;
    top: 55%;
    left: 15%;
    transform: translate(-50%, -50%)
}
#language-selector::after{
    content: url(data:image/png;base64,);
    position: absolute;
    top: 45%;
    left: 90%;
    transform: translate(-50%, -50%);
}
#lang-dropdown{
    color: #999;        
    border: 1px solid #333;
    border-radius: 2px;
    font-size: 1rem;
    background-color: black;
    width: inherit;
    height: 3.3rem;
    padding-left: 50px;   
    -moz-appearance:none;
    -webkit-appearance:none;
    appearance:none;
}
#footer-center-wrapper h6{
    font-size: .8rem;    
    margin: 1.5rem 0 2rem 0;
}
/*  /language selector */
/*  /FOOTER  */



